<template>
  <div class="about show" id="about">
    <h2>Settings</h2>
    <button @click="switchAudio()">
      <i class="fas fa-volume-up" id="audioIcon"></i>
    </button>
  </div>
</template>

<script>
export default {
  methods: {
    switchAudio() {
      document.getElementById("audioIcon").classList.toggle("fa-volume-up");
      document.getElementById("audioIcon").classList.toggle("fa-volume-mute");
      this.$store.dispatch("music");
      this.$emit("audioPlay");
    },
    mounted(){
      if (this.$store.state.music != true) {
        document.getElementById("audioIcon").classList.remove("fa-volume-up");
      document.getElementById("audioIcon").classList.add("fa-volume-mute");
      }
    }
  },
};
</script>

<style scoped>
button {
  height: 50px;
  width: 50px;
  background-color: #f3c892;
  color: #146356;
  font-size: 1.5em;
}
</style>